import React, { useState } from 'react'
import './Navbar.css'
import { ReactComponent as Logo } from '../../assets/icons/logo.svg'
import { ReactComponent as Burger } from '../../assets/icons/burger-menu.svg'
import { ReactComponent as CloseIcon } from '../../assets/icons/close-icon.svg'

function Navbar(props) {

  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const onClickBurger = (event) => {
    event.stopPropagation();
    const menu = document.querySelector('.nav-menu');
    const body = document.querySelector('body');
    body.addEventListener('click', onClickClose)
    menu.classList.toggle('open');
    setIsMenuOpen(true);
  }

  const onClickClose = (event) => {
    event.stopPropagation();
    const menu = document.querySelector('.nav-menu');
    menu.classList.remove('open');
    setIsMenuOpen(false);
  }

  const mobileIcon = () => {
    if (isMenuOpen) {
      return <CloseIcon className="close" onClick={onClickClose}/>
    }
    return <Burger className="burger" onClick={onClickBurger}/>
  }

  return (
    <nav className="nav">
      <div className="nav-logo">
        <Logo className="logo" />
      </div>

      <div className="nav-mobile">
        {mobileIcon()}
      </div>

      <ul className="nav-menu">
        {
          menus.map((menu, index) => (
            <li className="nav-item" key={index}>
              <a className={index === 0 ? 'nav-link active' : 'nav-link'} href={menu.link}>
                {menu.title}
              </a>
            </li>
          ))
        }
      </ul>
    </nav>
  )
}

const menus = [
  {
    title: 'About',
    link: '#about'
  },
  {
    title: 'Specializations',
    link: '#specializations'
  },
  {
    title: 'Projects',
    link: '#projects'
  },
  {
    title: 'Tools & Skills',
    link: '#tools'
  },
  {
    title: 'Testimonial',
    link: '#testimonial'
  },
  {
    title: 'Contact',
    link: '#contact'
  }
];

export default Navbar;

